<template>
  <div class="waja-player">
    <div class="waja-player-actions">
      <button :disabled="currentPanktiIndex === 0" @click="movePrevious()">
        <span class="icon-previous2"></span>
      </button>
      <span class="spinner icon-spinner3" v-if="isLoadingAudio"></span>
      <button @click="play('p'+currentPanktiIndex)" v-if="!isLoadingAudio">
        <span class="icon-play3" v-if="!isPlaying"></span>
        <span class="icon-stop2" v-else></span>
      </button>
      <button :disabled="currentPanktiIndex === this.shabad.panktis.length - 1" @click="moveNext()">
        <span class="icon-next2"></span>
      </button>
    </div>
    <div class="waja-wrapper-outer">
      <div class="waja-wrapper" :style="{transform: 'scale(' + wajaPlayerScaleVal + ')'}">
        <div class="pankti" v-if="currentPankti">
          <span
            class="word"
            :class="{ active: panktiItem ===  currentPanktiItem }"
            v-for="panktiItem in currentPankti"
            :key="panktiItem.word"
            @click="playWord(panktiItem)"
          >{{panktiItem.word}}</span>
        </div>

        <waja :activeKey="activeKey" />
      </div>
    </div>
  </div>
</template>

<script>
import waja from "./Waja";

export default {
  name: "WajaPlayer",
  components: { waja },
  props: ["shabad"],
  data() {
    return {
      currentPankti: null,
      activeKey: null,
      musicPlayer: null,
      currentPanktiItem: null,
      isLoadingAudio: false,
      isPlaying: false,
      wajaPlayerScaleVal: 1
    };
  },
  computed: {
    currentPanktiIndex() {
      return this.shabad.panktis.indexOf(this.currentPankti);
    }
  },
  created() {
    this.currentPankti = this.shabad.panktis[0];
    this._loadAudio();
    this.adjustWajaScaleValue();
    window.addEventListener("resize", this.adjustWajaScaleValue);
  },
  destroyed() {
    this.musicPlayer.destroy();
    window.removeEventListener("resize", this.adjustWajaScaleValue);
  },
  methods: {
    adjustWajaScaleValue() {
      const wajaWidth = 834;
      let scaleVal = window.innerWidth / wajaWidth;
      this.wajaPlayerScaleVal = scaleVal > 1 ? 1 : scaleVal;
    },
    play(audioSpriteKey) {
      if (this.isPlaying) {
        this.musicPlayer.stop();
        this.activeKey = null;
        this.currentPanktiItem = null;
        this.isPlaying = false;
        return;
      }

      if (this.musicPlayer.isLoaded) {
        this.isPlaying = true;
        let playerInstance = this.musicPlayer.play(audioSpriteKey);
        this._updateProgress(playerInstance);
      } else {
        this.isLoadingAudio = true;

        this.musicPlayer.play({
          sprite: audioSpriteKey,
          loaded: (err, sound, instance) => {
            this.isLoadingAudio = false;
            this.isPlaying = true;
            this._updateProgress(instance);
          }
        });
      }
    },

    playWord(panktiItem) {
      this.play(
        "p" +
          this.currentPanktiIndex +
          "w" +
          this.currentPankti.indexOf(panktiItem)
      );
    },

    movePrevious() {
      this.currentPankti = this.shabad.panktis[this.currentPanktiIndex - 1];
    },

    moveNext() {
      this.currentPankti = this.shabad.panktis[this.currentPanktiIndex + 1];
    },

    loadSprites() {
      let sprites = {};

      this.shabad.panktis.forEach((pankti, panktiIndex) => {
        pankti.forEach((panktiItem, panktiItemIndex) => {
          let spriteKey = "p" + panktiIndex + "w" + panktiItemIndex; // e.g. p0w0

          sprites[spriteKey] = {
            start: panktiItem.keys[0].audioStart,
            end: panktiItem.keys[panktiItem.keys.length - 1].audioEnd
          };
        });

        let panktiStartEndKey = "p" + panktiIndex; //e.g. p0
        let lastPanktiItem = pankti[pankti.length - 1];

        sprites[panktiStartEndKey] = {
          start: pankti[0].keys[0].audioStart,
          end: lastPanktiItem.keys[lastPanktiItem.keys.length - 1].audioEnd
        };
      });

      console.log("Generated sprites", sprites);

      this.musicPlayer.removeSprites();
      this.musicPlayer.addSprites(sprites);
    },

    _updateProgress(instance) {
      instance
        .on("end", () => {
          this.activeKey = null;
          this.currentPanktiItem = null;
          this.isPlaying = false;
        })
        .on("progress", (progress, duration) => {
          var audioTime = progress * duration; //progress is from 0.1 to 1

          this.currentPanktiItem = this.currentPankti.find(
            panktiItem =>
              audioTime >= panktiItem.keys[0].audioStart &&
              audioTime <= panktiItem.keys[panktiItem.keys.length - 1].audioEnd
          );

          if (this.currentPanktiItem) {
            this.activeKey = this.currentPanktiItem.keys.find(
              key => audioTime >= key.audioStart && audioTime <= key.audioEnd
            );
          }
        });
    },

    _loadAudio() {
      this.musicPlayer = window.PIXI.sound.Sound.from({
        url: this.shabad.audio,
        // preload: true, this causes problem on mobile devices and keys dont play when playing for the first time.
        singleInstance: true
      });

      this.loadSprites();
    }
  }
};
</script>

<style scoped>
.waja-player-actions {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 10px;
}

.waja-player-actions button {
  background: none;
  border: none;
  font-size: 2rem;
}

.spinner {
  font-size: 2rem;
}

.waja-player-actions button:focus {
  outline: none;
}

.waja-player-actions button[disabled] {
  opacity: 0.2;
}

.waja-player-actions button:active {
  color: dimgrey;
}

.waja-wrapper-outer {
  height: 0;
  padding-bottom: 36%;
  overflow: hidden;
}

.waja-wrapper {
  background: black;
  width: 834px;
  margin: 0 auto;
  padding: 10px;
  transform-origin: top left;
}

.pankti {
  text-align: center;
  color: white;
  font-size: 2rem;
  margin-bottom: 10px;
}

.word {
  margin-right: 5px;
  border-bottom: 2px solid white;
  cursor: pointer;
}

.word.active {
  background: grey;
}
</style>