<template>
  <div class="shabad-editor">
    <div class="accordion">
      <!-- eslint-disable vue/require-v-for-key -->
      <div v-for="(pankti, panktiIndex) in shabad.panktis">
        <div class="title active" @click="toggleAccordion(panktiIndex)">
          {{getPanktiShabad(pankti)}}
          <button
            @click="deletePankti(panktiIndex)"
            class="text-only"
            title="Delete pankti"
          >
            <span class="icon-bin"></span>
          </button>
        </div>
        <div class="content" :class="{active: panktiIndex === activeIndex}">
          <table>
            <tr v-for="(panktiItem, panktiItemIndex) in pankti">
              <td>
                <input type="text" v-model="panktiItem.word" />
                <button @click="removeWord(pankti, panktiItemIndex)" class="text-only">
                  <span class="icon-bin"></span>
                </button>
              </td>
              <td>
                <div class="fields" v-for="(key) in panktiItem.keys">
                  <div class="field">
                    <label>Type</label>
                    <input type="text" v-model="key.type" />
                  </div>
                  <div class="field">
                    <label>Index</label>
                    <input type="number" v-model.number="key.index" />
                  </div>
                  <div class="field">
                    <label>Start</label>
                    <input type="number" v-model.number="key.audioStart" @input="forceUpdate" />
                  </div>
                  <div class="field">
                    <label>End</label>
                    <input type="number" v-model.number="key.audioEnd" @input="forceUpdate" />
                  </div>

                  <button @click="removeKey(panktiItem.keys, key)" class="text-only">
                    <span class="icon-bin"></span>
                  </button>
                </div>

                <button @click="addAnotherKey(panktiItem)" class="button green sm">Add another key</button>
              </td>
            </tr>
          </table>
          <button @click="addAnotherWord(pankti)" class="button green sm">Add Another Word</button>
        </div>
      </div>
      <!-- eslint-enable -->
    </div>
    <button @click="addAnotherPankti()" class="button green">Add another pankti</button>
    <button @click="outputJson()" class="button green">Output Json</button>
  </div>
</template>

<script>
export default {
  name: "ShabadEditor",
  props: ["shabad"],
  data() {
    return {
      activeIndex: 0
    };
  },
  methods: {
    getPanktiShabad(pankti) {
      let panktiShabad = pankti
        .map(panktiItem => panktiItem.word)
        .join(" ")
        .trim();

      return panktiShabad.length > 0 ? panktiShabad : "AWAITING TITLE";
    },

    toggleAccordion(index) {
      if (index === this.activeIndex) this.activeIndex = null;
      else this.activeIndex = index;
    },
    deletePankti(panktiIndex) {
      this.shabad.panktis.splice(panktiIndex, 1);
      this.$emit("update");
    },
    addAnotherPankti() {
      let newPankti = [];
      this.shabad.panktis.push(newPankti);
      this.addAnotherWord(newPankti);
      this.activeIndex = this.shabad.panktis.length - 1;
    },
    addAnotherWord(pankti) {
      let newPanktiItem = { word: "", keys: [] };
      pankti.push(newPanktiItem);
      // this.addAnotherKey(newPanktiItem);
    },
    removeWord(pankti, panktiItemIndex) {
      pankti.splice(panktiItemIndex, 1);
      this.$emit("update");
    },
    addAnotherKey(panktiItem) {
      panktiItem.keys.push({
        type: "",
        index: 0,
        audioStart: "0",
        audioEnd: "0"
      });
      this.$emit("update");
    },
    removeKey(keys, key) {
      keys.splice(keys.indexOf(key), 1);
      this.$emit("update");
    },
    forceUpdate() {
      this.$emit("update");
    },
    outputJson() {
      console.log(this.shabad);
      console.log(JSON.stringify(this.shabad));
      navigator.clipboard.writeText(JSON.stringify(this.shabad)).then(
        function() {
          console.log("Async: Copying to clipboard was successful!");
        },
        function(err) {
          console.error("Async: Could not copy text: ", err);
        }
      );
    }
  }
};
</script>

