<template>
  <div class="shabad-page">
    <div class="shabad-info" v-if="shabad">
      {{shabad.desc}} Read the
      <a :href="shabad.gurbaniUrl" target="_blank">full shabad here</a>.
    </div>
    <wajaPlayer ref="wajaPlayer" :shabad="shabad" />
    <div class="credits" v-if="shabad && shabad.credits">
      🙏 {{shabad.credits}}
      <span v-if="shabad.creditsUrl">
        Be sure to
        <a :href="shabad.creditsUrl" target="_blank">check it out</a>.
      </span>
    </div>

    <shabadEditor :shabad="shabad" v-if="isAdmin" @update="updateSprites" />
  </div>
</template>

<script>
import shabads from "./../assets/shabads.json";
import wajaPlayer from "./WajaPlayer";
import shabadEditor from "./ShabadEditor";

export default {
  name: "Home",
  components: { wajaPlayer, shabadEditor },
  data() {
    return { shabad: null, isAdmin: false };
  },
  created() {
    this.shabad = shabads.data.find(
      shabad => shabad.slug == this.$route.params.shabadSlug
    );

    this.isAdmin = this.$route.query.isAdmin === "true";
  },
  methods: {
    updateSprites() {
      this.$refs.wajaPlayer.loadSprites();
    }
  }
};
</script>

