import Vue from "vue";
import Router from "vue-router";
import Home from "@/components/Home";
import ShabadComponent from "@/components/Shabad";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home,
    },
    {
      path: "/shabads/:shabadSlug",
      name: "Shabad",
      component: ShabadComponent,
    },
  ],
});
